export default {
	name: 'alertInput',
	props: {
		title: {
			type: String,
			default: '提示'
		},
		placeholder: {
			type: String,
			default: '请点击输入'
		},
		name: {
			type: String,
			default: 'text'
		},
		type: {
			type: String,
			default: 'text'
		},
		value: {
			type: String,
			default: ''
		},
		cancelColor: {
			type: String,
			default: '#999999'
		},
		confirmColor: {
			type: String,
			default: '#333333'
		},
		cancelText: {
			type: String,
			default: '取消'
		},
		confirmText: {
			type: String,
			default: '确定'
		},
	},
	data() {
		return {
			h1:true,
			h2:false

		};
	},
	methods: {
		formSubmit: function(e) {
			console.log(e)
			let _formdata = e.detail.value
			console.log(_formdata);
			if(_formdata.content==''){
				uni.showToast({
					title:'请先填写内容'
				});
			}else{
				if(this.type=='mul'){
					if(this.h1==true){
						var xz='单向';
					}else if(this.h2==true){
						var xz='双向';
					}
					this.$emit('confirm',[ _formdata.content,xz]);
				}else{
					this.$emit('confirm', _formdata.content);
				}
			}
			
		},
		formReset: function(e) {
			this.$emit('cancel')
		},
		change(e){
			console.log(e);
			if(e==1){
				if(this.h1==true){
					this.h1=false
				}else{
					this.h1=true;
					this.h2=false;
				}
			}else{
				if(this.h2==true){
					this.h2=false
				}else{
					this.h2=true;
					this.h1=false;
				}
			}
		}
	}
}
